<template>
  <div id="d-table-test">
    <vueshowpdf
      @closepdf="closepdf"
      v-model="isshowpdf"
      :pdfurl="'./wendang.pdf'"
      @pdferr="pdferr"
      :maxscale="4"
      :minscale="0.6"
      :scale="1.0"
    ></vueshowpdf>
    <div class="prompt_class">功能还在完善阶段哦~</div>
    <div style="width: 100%; height: 100%; float: left">
      <table-component
        titleName="标题"
        componentKey="tableKey1"
        :loading="loading"
        ref="dtable"
        :tableColumns="tableColumns"
        :tableData="tableData"
        :userPolicy="userPolicy"
        :currentUserPolicy="currentPolicy"
        :QueryComplate="QueryComplate"
        :UserPolicyComplate="UserPolicyComplate"
        :ChangeUserPolicy="ChangeUserPolicy"
        :SortVisible="true"
        :enums="enums"
        :total="total"
        :RowClick="RowClick"
        :SelectionChange="SelectionChange"
        :DefaultFirstRow="true"
        :AddVisible="true"
        :EditVisible="true"
        :DelVisible="true"
        :OperateColumnVisible="true"
        :ExportVisible="false"
        :AddData="AddDataFun"
        :EditData="EditDataFun"
        :DelRow="delRowFun"
        :DelRows="delRowsFun"
        rowKey="id"
        rowChildren="children"
      >
        <template slot="column" slot-scope="scope">
          <el-checkbox
            v-model="scope.row.col06"
            true-label="YES"
            false-label="NO"
          ></el-checkbox>
        </template>

        <template slot="header">
          <el-button @click="click1"> 插槽按钮 </el-button>
          <el-button @click="useComponent"> 使用文档 </el-button>
        </template>
      </table-component>
    </div>
  </div>
</template>
<script>
import { config1, config2 } from "./configData/config.js";
import vueshowpdf from "vueshowpdf";
export default {
  name: "DTABLE",
  components: { vueshowpdf },
  data() {
    return {
      tableColumns: [
        {
          prop: "col01",
          label: "数字列",
          dataType: "NUMERICAL",
          width: "80",
          align: "center",
          fixed: 0,
          sortBy: 1,
          showType: "TEXT",
          fshow: 1,
        },
        {
          prop: "col02",
          label: "字符串列",
          dataType: "CHARACTER",
          width: "120",
          align: "center",
          fixed: 0,
          sortBy: 2,
          showType: "TEXT",
          fshow: 1,
        },
        {
          prop: "col03",
          label: "时间列",
          dataType: "DATE",
          width: "150",
          align: "center",
          fixed: 0,
          sortBy: 3,
          showType: "TEXT",
          fshow: 1,
        },
        {
          prop: "col05",
          label: "枚举列",
          dataType: "CHARACTER",
          width: "120",
          align: "center",
          fixed: 0,
          sortBy: 5,
          showType: "TEXT",
          fshow: 1,
          enumKey: "enumKeyName",
        },
        {
          prop: "col06",
          label: "作用域插槽列",
          dataType: "CHARACTER",
          width: "80",
          align: "center",
          fixed: 0,
          sortBy: 6,
          showType: "TEXT",
          fshow: 1,
          fscope: true,
        },
      ],
      isshowpdf: false,
      tableData: [],
      userPolicy: [
        {
          label: "预设1",
          value: "1",
        },
        {
          label: "预设2",
          value: "2",
        },
      ],
      currentPolicy: {},
      enums: [
        {
          key: "enumKeyName",
          values: [
            {
              label: "男",
              value: "M",
            },
            {
              label: "女",
              value: "F",
            },
          ],
        },
      ],
      userCurrentConfig: [],
      total: 1000000,
      loading: false,
    };
  },
  created() {
    this.GetUserPolicy();
    this.createData();

    this.userCurrentConfig = [config1, config2];
  },
  mounted() {
    this.currentPolicy = config1;
    this.$refs.dtable.InitParams();
  },
  methods: {
    QueryComplate(a, b, _screens, _sorts) {
      console.log({ _screens, _sorts });

      if (_sorts) {
        if (_screens) {
        }

        function sortBy(attr, rev) {
          if (rev == undefined) {
            rev = 1;
          } else {
            rev ? 1 : -1;
          }
          return function (a, b) {
            a = a[attr];
            b = b[attr];
            if (a < b) {
              return rev * -1;
            }
            if (a > b) {
              return rev * 1;
            }
            return 0;
          };
        }
        if (_sorts) {
          _sorts.forEach((item) => {
            const key = item.property;

            this.tableData.sort(sortBy(key, item.sortOrder == "asc" ? 1 : -1));
            this.$refs.dtable.tableDataRun = this.tableData;
            setTimeout(() => {
              console.log(this.tableData);
            }, 3000);
          });
        }
      } else {
        this.createData();
      }
    },
    createData() {
      console.log("createData>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>");
      this.loading = true;
      new Promise((resolve, reject) => {
        setTimeout(() => {
          const arr = [];
          for (var i = 0; i < 100; i++) {
            arr.push({
              col01: i + i,
              col02: "string " + " -1",
              col03: Date.now() + Math.random() * 1000000000,
              col04: "2022-07-19 05:20:22",
              col05: Math.random() > 0.5 ? "F" : "M",
              col06: Math.random() > 0.5 ? "YES" : "NO",
              id: this.uuid() + Date.now() + Math.random() * 1000000000,
            });
          }
          this.tableData = arr;
          resolve();
        }, 1000);
      }).finally(() => {
        this.loading = false;
      });
    },
    uuid() {
      return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
          var r = (Math.random() * 16) | 0,
            v = c == "x" ? r : (r & 0x3) | 0x8;
          return v.toString(16);
        }
      );
    },
    // 保存用户版本事件
    UserPolicyComplate(_userPolicy) {
      console.log({ _userPolicy });

      _userPolicy.versionName;
      const id = this.uuid();
      const index = this.userCurrentConfig.findIndex(
        (item) => item.versionName == _userPolicy.versionName
      );
      if (index > -1) {
        this.$message.warning("版本名称不能一样哦~");
        return;
      }
      // if (index > -1) {
      //   // 编辑
      //   this.userCurrentConfig[index] = {
      //     versionValue: id,
      //     ..._userPolicy.conf,
      //   };

      //   this.$message.warning("更改成功");
      // } else {
      // 新增
      this.userPolicy.push({
        label: _userPolicy.versionName,
        value: String(this.userCurrentConfig.length + 1),
      });

      _userPolicy.conf.id = String(this.userCurrentConfig.length + 1);
      this.userCurrentConfig.push({
        versionName: _userPolicy.versionName,
        versionValue: String(this.userCurrentConfig.length + 1),
        ..._userPolicy.conf,
      });

      // 调用后台API进行数据保存
      this.$message.success("新增版本成功");
      // }
    },
    // 获取用户版本配置
    GetUserPolicy() {
      // 获取菜单组件版本list
      // this.userPolicy = [];
      // this.userPolicy = [
      //   {
      //     label:"测试77777",
      //     value:"69a2c32f-cbc7-402d-a576-1c1f3ffaa9c7"
      //   },
      //   {
      //     label:"测试",
      //     value:"003b3120-bb35-4631-86db-6a908c4ddea6"
      //   }
      // ]
    },
    // 根据id获取版本配置
    ChangeUserPolicy(versionValue) {
      console.log({ versionValue }, this.userCurrentConfig);
      const index = this.userCurrentConfig.findIndex(
        (item) => item.versionValue == versionValue
      );
      if (index > -1) {
        this.currentPolicy = this.userCurrentConfig[index];

        console.log("this.currentPolicy", this.currentPolicy);
      } else {
        this.$message.warning("该配置有问题");
      }

      // if (value == 1) {
      //   this.currentPolicy = config1;
      // } else if (value == 2) {
      //   this.currentPolicy = config2;
      // }
    },
    click1() {
      this.$message.error("这是按钮1自定义事件");
    },
    useComponent() {
      this.isshowpdf = true;
    },
    closepdf() {
      this.isshowpdf = false;
    },
    pdferr() {},
    click2() {
      this.$message.error("这是按钮2自定义事件");
    },
    click3() {
      this.$message.error("这是按钮3自定义事件");
    },
    RowClick(row, column, event) {
      console.log("RowClick", row, column, event);
    },
    SelectionChange(selection) {
      console.log("SelectionChange", selection);
    },
    AddDataFun(tableData) {
      console.log("新建提交的表单数据：", tableData);
    },
    EditDataFun(tableData) {
      console.log("编辑提交的表单数据：", tableData);
    },
    delRowFun(row) {
      console.log("删除行：", row);
    },
    delRowsFun(rows) {
      console.log("删除多行：", rows);
    },
  },
};
</script>
<style>
#d-table-test {
  width: 100%;
  height: 100vh;
}

.prompt_class {
  position: fixed;
  bottom: 20px;
  right: 100px;
  font-size: 50px;
  color: pink;
  opacity: 1;
  z-index: 999;
  user-select: none;
}

.container {
  font-family: PingFang SC;
  width: 100%;
  height: 500px;
}
</style>
